// Asynchronous fetching of the room. This is so that the mobile app can use the
// same controller. It doesn't know the room straight away
angular.module('opentok-meet').factory('RoomService', ['$http', 'baseURL', '$window', 'room',
  function ($http, baseURL, $window, room) {
    return {
      getRoom: function () {
        // return $http.get(baseURL + room).then(function(response) {
        //   return response.data;
        // });
        return new Promise((resolved, reject) => {

          try {
            let roomData = {};
            if (getQueryVariable('token')) {
              roomData.p2p = false;
              roomData.room = getQueryVariable('room');
              roomData.apiKey = getQueryVariable('apiKey');
              roomData.sessionId = getQueryVariable('sessionId');
              roomData.token = getQueryVariable('token');
              resolved(roomData);
            } else {
              $.ajax({
                url: baseURL + '/host/' + window.localStorage.getItem('username') + '/room/' + window.localStorage.getItem('room'),
                type: 'get',
                headers: {
                  'content-type': 'application/x-www-form-urlencoded',
                  'Authorization': window.localStorage.getItem('authorization')
                },
                contentType: 'application/json',
                dataType: 'json'
              }).done(function (e) {
                roomData.p2p = e.p2p;
                roomData.room = e.room;
                roomData.apiKey = e.apiKey;
                roomData.sessionId = e.sessionId;
                roomData.token = e.token;
                resolved(roomData);
              }).fail(function (e) {
                alert('the room not exits');
                window.location.href = './index.html';
                console.error(e);
              });
            }
          }
          catch (e) {
            reject(e);
          }
        });
      },
      changeRoom: function () {
        $window.location.href = 'index.html';
      },
      startArchiving: function (sessionId, options) {
        return new Promise((resolved, reject) => {
          $.ajax({
            url: baseURL + '/archive/start/' + sessionId,
            type: 'POST',
            headers: {
              'content-type': 'application/x-www-form-urlencoded',
              'Authorization': window.localStorage.getItem('authorization')
            },
            contentType: 'application/json',
            dataType: 'json',
            data:  {archiveOptions: options}
          }).done(resolved).fail(reject);
        });
      },
      stopArchiving: function(archiveId) {
        return new Promise((resolved, reject) => {
          $.ajax( {
            url: baseURL + '/archive/stop/' + archiveId,
            type: 'get',
            headers: {
              'content-type': 'application/x-www-form-urlencoded',
              'Authorization': window.localStorage.getItem('authorization')
            }
          }).done(resolved).fail(reject);
        });
      }
    };
  }
]);



